"use client";

import { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Separator } from "@/components/ui/separator";

interface Venue {
  id: string;
  name: string;
  description: string;
  address: string;
  city: string;
  state: string;
  zipCode?: string;
  category: string;
  capacity: number;
  price: number;
  images: string[];
  amenities: string[];
  rules: string[];
  averageRating: number;
  reviewCount: number;
  owner: {
    id: string;
    name: string;
    phone: string;
    email: string;
  };
  reviews?: any[];
}

interface FormData {
  nome: string;
  email: string;
  telefone: string;
  dataEvento: string;
  numeroConvidados: string;
  tipoEvento: string;
  mensagem: string;
}

// Função para mapear categorias do banco para display
const mapCategoryToDisplay = (category: string) => {
  const categoryMap: { [key: string]: string } = {
    'CHACARA': 'Chácara',
    'RESTAURANTE': 'Restaurante', 
    'SALAO': 'Salão',
    'POUSADA': 'Pousada',
    'CLUBE': 'Clube',
    'ESPACO_RURAL': 'Espaço Rural',
    'HOTEL': 'Hotel',
    'OUTROS': 'Outros'
  };
  return categoryMap[category] || category;
};

export default function VenueDetailPage() {
  const params = useParams();
  const venueId = params?.id as string;
  
  const [venue, setVenue] = useState<Venue | null>(null);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [formData, setFormData] = useState<FormData>({
    nome: "",
    email: "",
    telefone: "",
    dataEvento: "",
    numeroConvidados: "",
    tipoEvento: "",
    mensagem: ""
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState("");
  const [error, setError] = useState("");
  const [isLoading, setIsLoading] = useState(true);

  // Buscar dados do venue
  useEffect(() => {
    const fetchVenue = async () => {
      if (!venueId) return;
      
      setIsLoading(true);
      setError("");
      
      try {
        const response = await fetch(`/api/venues/${venueId}`);
        
        if (!response.ok) {
          if (response.status === 404) {
            setError("Espaço não encontrado");
          } else {
            setError("Erro ao carregar espaço");
          }
          return;
        }

        const data: Venue = await response.json();
        setVenue(data);
      } catch (err) {
        console.error('Erro ao buscar venue:', err);
        setError("Erro ao carregar espaço");
      } finally {
        setIsLoading(false);
      }
    };

    fetchVenue();
  }, [venueId]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setError("");

    try {
      // Validação básica
      if (!formData.nome || !formData.email || !formData.telefone || !formData.dataEvento) {
        throw new Error("Por favor, preencha todos os campos obrigatórios.");
      }

      // Simular envio (futuramente conectar com API de bookings)
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      setSubmitMessage("Solicitação enviada com sucesso! O proprietário entrará em contato em breve.");
      setFormData({
        nome: "",
        email: "",
        telefone: "",
        dataEvento: "",
        numeroConvidados: "",
        tipoEvento: "",
        mensagem: ""
      });
    } catch (err) {
      setError(err instanceof Error ? err.message : "Erro ao enviar solicitação. Tente novamente.");
    } finally {
      setIsSubmitting(false);
    }
  };

  if (isLoading) {
    return (
      <div className="container mx-auto px-4 py-16">
        <div className="animate-pulse space-y-8">
          <div className="h-8 bg-muted rounded w-1/3"></div>
          <div className="h-96 bg-muted rounded"></div>
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            <div className="lg:col-span-2 space-y-6">
              <div className="h-6 bg-muted rounded w-1/4"></div>
              <div className="space-y-2">
                <div className="h-4 bg-muted rounded"></div>
                <div className="h-4 bg-muted rounded w-3/4"></div>
              </div>
            </div>
            <div className="h-96 bg-muted rounded"></div>
          </div>
        </div>
      </div>
    );
  }

  if (error && !venue) {
    return (
      <div className="container mx-auto px-4 py-16 text-center">
        <h1 className="text-2xl font-bold mb-4">Espaço não encontrado</h1>
        <p className="text-muted-foreground mb-8">O espaço que você está procurando não existe ou foi removido.</p>
        <Button asChild>
          <Link href="/listings">Voltar para listagens</Link>
        </Button>
      </div>
    );
  }

  if (!venue) return null;

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Breadcrumb */}
      <nav className="mb-6">
        <div className="flex items-center space-x-2 text-sm text-muted-foreground">
          <Link href="/" className="hover:text-foreground">Início</Link>
          <span>/</span>
          <Link href="/listings" className="hover:text-foreground">Espaços</Link>
          <span>/</span>
          <span className="text-foreground">{venue.name}</span>
        </div>
      </nav>

      {/* Header */}
      <div className="mb-8">
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4 mb-4">
          <div>
            <h1 className="text-3xl lg:text-4xl font-bold mb-2">{venue.name}</h1>
            <div className="flex items-center gap-4 text-muted-foreground">
              <span>{venue.city}, {venue.state}</span>
              {venue.averageRating > 0 && (
                <>
                  <span>•</span>
                  <div className="flex items-center gap-1">
                    <span className="text-yellow-500">★</span>
                    <span className="font-medium text-foreground">{venue.averageRating.toFixed(1)}</span>
                    <span>({venue.reviewCount} avaliações)</span>
                  </div>
                </>
              )}
            </div>
          </div>
          <div className="flex items-center gap-2">
            <Badge variant="secondary">{mapCategoryToDisplay(venue.category)}</Badge>
            <Badge variant="outline">Até {venue.capacity} pessoas</Badge>
          </div>
        </div>
      </div>

      {/* Image Gallery */}
      <div className="mb-8">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-4 h-96 lg:h-[500px]">
          <div className="lg:col-span-3">
            <img
              src={venue.images[currentImageIndex] || 'https://placehold.co/800x600?text=Imagem+não+disponível'}
              alt={`${venue.name} - Imagem ${currentImageIndex + 1}`}
              className="w-full h-full object-cover rounded-lg cursor-pointer"
              onClick={() => setCurrentImageIndex((currentImageIndex + 1) % venue.images.length)}
            />
          </div>
          <div className="hidden lg:flex flex-col gap-4">
            {venue.images.slice(1, 4).map((image: string, index: number) => (
              <img
                key={index}
                src={image || 'https://placehold.co/400x300?text=Imagem+não+disponível'}
                alt={`${venue.name} - Miniatura ${index + 2}`}
                className="w-full h-full object-cover rounded-lg cursor-pointer hover:opacity-80 transition-opacity"
                onClick={() => setCurrentImageIndex(index + 1)}
              />
            ))}
          </div>
        </div>
        
        {/* Mobile Image Navigation */}
        <div className="lg:hidden flex justify-center mt-4 gap-2">
          {venue.images.map((_: any, index: number) => (
            <button
              key={index}
              onClick={() => setCurrentImageIndex(index)}
              className={`w-3 h-3 rounded-full transition-colors ${
                index === currentImageIndex ? 'bg-primary' : 'bg-muted'
              }`}
            />
          ))}
        </div>
      </div>

      {/* Content */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Main Content */}
        <div className="lg:col-span-2 space-y-8">
          {/* Description */}
          <section>
            <h2 className="text-2xl font-bold mb-4">Sobre o espaço</h2>
            <p className="text-muted-foreground leading-relaxed">{venue.description}</p>
          </section>

          {/* Amenities */}
          <section>
            <h2 className="text-2xl font-bold mb-4">Comodidades</h2>
            <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
              {venue.amenities.map((amenity: string, index: number) => (
                <div key={index} className="flex items-center gap-2 text-sm">
                  <div className="w-2 h-2 bg-primary rounded-full"></div>
                  <span>{amenity}</span>
                </div>
              ))}
            </div>
          </section>

          {/* Rules */}
          <section>
            <h2 className="text-2xl font-bold mb-4">Regras do espaço</h2>
            <ul className="space-y-2">
              {venue.rules.map((rule: string, index: number) => (
                <li key={index} className="flex items-start gap-2 text-sm text-muted-foreground">
                  <div className="w-1 h-1 bg-muted-foreground rounded-full mt-2 flex-shrink-0"></div>
                  <span>{rule}</span>
                </li>
              ))}
            </ul>
          </section>

          {/* Location */}
          <section>
            <h2 className="text-2xl font-bold mb-4">Localização</h2>
            <div className="space-y-2">
              <p className="text-muted-foreground">{venue.address}</p>
              <p className="text-muted-foreground">{venue.city}, {venue.state} {venue.zipCode && `- ${venue.zipCode}`}</p>
              <div className="w-full h-64 bg-muted rounded-lg flex items-center justify-center">
                <p className="text-muted-foreground">
                  Mapa será integrado em breve (OpenStreetMap + Leaflet)
                </p>
              </div>
            </div>
          </section>
        </div>

        {/* Booking Card */}
        <div className="lg:sticky lg:top-8 lg:self-start">
          <Card className="shadow-lg border-0">
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                <span>Solicitar Reserva</span>
                <div className="text-right">
                  <div className="text-2xl font-bold">R$ {venue.price.toLocaleString('pt-BR')}</div>
                  <div className="text-sm text-muted-foreground">por evento</div>
                </div>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              {submitMessage && (
                <div className="p-4 bg-green-50 border border-green-200 rounded-lg">
                  <p className="text-green-800 text-sm">{submitMessage}</p>
                </div>
              )}

              <form onSubmit={handleSubmit} className="space-y-4">
                <div className="grid grid-cols-1 gap-4">
                  <div>
                    <Label htmlFor="nome">Nome completo *</Label>
                    <Input
                      id="nome"
                      name="nome"
                      type="text"
                      value={formData.nome}
                      onChange={handleInputChange}
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="email">E-mail *</Label>
                    <Input
                      id="email"
                      name="email"
                      type="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="telefone">Telefone *</Label>
                    <Input
                      id="telefone"
                      name="telefone"
                      type="tel"
                      value={formData.telefone}
                      onChange={handleInputChange}
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="dataEvento">Data do evento *</Label>
                    <Input
                      id="dataEvento"
                      name="dataEvento"
                      type="date"
                      value={formData.dataEvento}
                      onChange={handleInputChange}
                      min={new Date().toISOString().split('T')[0]}
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="numeroConvidados">Número de convidados</Label>
                    <Input
                      id="numeroConvidados"
                      name="numeroConvidados"
                      type="number"
                      value={formData.numeroConvidados}
                      onChange={handleInputChange}
                      max={venue.capacity}
                      placeholder={`Máximo ${venue.capacity}`}
                    />
                  </div>

                  <div>
                    <Label htmlFor="tipoEvento">Tipo de evento</Label>
                    <select
                      id="tipoEvento"
                      name="tipoEvento"
                      value={formData.tipoEvento}
                      onChange={handleInputChange}
                      className="w-full p-2 border border-border rounded-md bg-background"
                    >
                      <option value="">Selecione o tipo</option>
                      <option value="casamento">Casamento</option>
                      <option value="aniversario">Aniversário</option>
                      <option value="corporativo">Evento Corporativo</option>
                      <option value="formatura">Formatura</option>
                      <option value="confraternizacao">Confraternização</option>
                      <option value="outro">Outro</option>
                    </select>
                  </div>

                  <div>
                    <Label htmlFor="mensagem">Mensagem adicional</Label>
                    <Textarea
                      id="mensagem"
                      name="mensagem"
                      value={formData.mensagem}
                      onChange={handleInputChange}
                      placeholder="Conte mais sobre seu evento..."
                      rows={3}
                    />
                  </div>
                </div>

                {error && (
                  <div className="p-4 bg-red-50 border border-red-200 rounded-lg">
                    <p className="text-red-800 text-sm">{error}</p>
                  </div>
                )}

                <Button 
                  type="submit" 
                  className="w-full" 
                  size="lg"
                  disabled={isSubmitting}
                >
                  {isSubmitting ? "Enviando..." : "Solicitar Reserva"}
                </Button>
              </form>

              <Separator />

              {/* Owner Info */}
              <div className="space-y-3">
                <h3 className="font-semibold">Proprietário</h3>
                <div className="space-y-2 text-sm">
                  <p><strong>{venue.owner.name}</strong></p>
                  <p className="text-muted-foreground">Responde em até 2 horas</p>
                  <div className="flex flex-col gap-2">
                    <Button variant="outline" size="sm" asChild>
                      <a href={`tel:${venue.owner.phone}`}>
                        {venue.owner.phone}
                      </a>
                    </Button>
                    <Button variant="outline" size="sm" asChild>
                      <a href={`mailto:${venue.owner.email}`}>
                        Enviar e-mail
                      </a>
                    </Button>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
